:root {
  --primary-color: #8b7a41; /* Egyptian gold */
  --secondary-color: #443d12b8; /* Deep blue of the Nile */
  --background-color: #6c4e2e; /* Dark sand color */
  --text-color: #f0e6d2; /* Papyrus color */
  --accent-color: #b22222; /* Egyptian red */
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: 'Papyrus', sans-serif;
  margin: 0;
  padding: 0;
  background-image: url('/images/backgrounds/bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  min-height: 100vh;
  overflow-x: hidden;
}

/* Rest of the CSS remains the same */

/* You may need to add this if there's still some horizontal movement */
html, body {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

/* Ensure the content doesn't cause horizontal scrolling */
.content-wrapper {
  width: 100%;
  max-width: 100vw;
  overflow-x: hidden;
}

/* Add this if you have any absolutely positioned elements that might be causing overflow */
* {
  box-sizing: border-box;
}

.top-section {
  background-color: rgba(0, 0, 0, 0.7);
  padding: 10px 5px;
}

.main-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.icon-button {
  background: none;
  border: none;
  padding: 5px;
}

.icon-button img {
  width: 28px;
  height: 28px;
}

.profile-button {
  background-color: #4a4a4a;
  color: #fff;
  border: none;
  padding: 8px 15px;
  border-radius: 20px;
  font-size: 16px;
  font-weight: bold;
}

.player-stats {
  display: flex;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 10px 18px;
}

.stat-item {
  display: flex;
  align-items: center;
  color: #fff;
}

.stat-item img {
  width: 20px;
  height: 20px;
  margin-right: 6px;
}

.stat-item span {
  font-size: 14px;
  font-weight: bold;
}

/* For slightly larger mobile devices */
@media (min-width: 360px) {
  .icon-button img {
    width: 32px;
    height: 32px;
  }

  .profile-button {
    font-size: 18px;
    padding: 10px 20px;
  }

  .stat-item img {
    width: 24px;
    height: 24px;
    margin-right: 8px;
  }

  .stat-item span {
    font-size: 16px;
  }
}
.round-button img {
  width: 20px;
  height: 20px;
}

.round-button:active, .profile-button:active {
  background-color: #5a5a50;
  border-color: #e5c100;
  transform: scale(0.95);
}

/* For devices with a bit more screen space */
@media (min-width: 375px) {
  .stat-item span {
    font-size: 14px;
  }

  .round-button, .profile-button {
    width: 45px;
    height: 45px;
  }

  .profile-button {
    width: auto;
  }
}

/* For larger mobile devices or small tablets */
@media (min-width: 480px) {
  .top-section {
    padding: 15px 10px;
  }

  .player-stats {
    padding: 8px 15px;
  }

  .stat-item {
    padding: 3px 8px;
  }

  .stat-item img {
    width: 24px;
    height: 24px;
  }

  .stat-item span {
    font-size: 16px;
  }

  .round-button, .profile-button {
    width: 50px;
    height: 50px;
    font-size: 14px;
  }

  .profile-button {
    width: auto;
    padding: 0 20px;
  }
}


button {
  background-color: #3b3a34; /* Dark sandstone color */
