/* Profile Overlay */
.profile-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
}

.profile-card {
  background: linear-gradient(145deg, #3b3a34, #6c4e2e);
  border: 2px solid #ffd700;
  padding: 30px;
  border-radius: 15px;
  color: #f0e6d2;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  width: 90%;
  max-width: 400px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-card h2 {
  color: #ffd700;
  margin-bottom: 20px;
  font-size: 2rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.close-button {
  position: absolute;
  top: 3px;
  left: 130px;
  background: none;
  border: none;
  color: #ffd700;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 5px;
  line-height: 1;
  transition: transform 0.3s ease;
  z-index: 1;
}

.close-button:hover {
  transform: rotate(-90deg);
}

.character-section {
  margin: 20px 0;
  position: relative;
}

.player-name {
  font-size: 1.5em;
  margin-top: 10px;
}

.glow-blue {
  color: #fff;
  animation: glow-blue 1.5s ease-in-out infinite alternate;
}

.glow-pink {
  color: #fff;
  animation: glow-pink 1.5s ease-in-out infinite alternate;
}

/* Level-based glow intensities */
.level-1 {
  filter: drop-shadow(0 0 2px var(--glow-color));
}

.level-2 {
  filter: drop-shadow(0 0 4px var(--glow-color)) drop-shadow(0 0 6px var(--glow-color));
}

.level-3 {
  filter: drop-shadow(0 0 6px var(--glow-color)) drop-shadow(0 0 8px var(--glow-color)) drop-shadow(0 0 10px var(--glow-color));
}

.level-4 {
  filter: drop-shadow(0 0 8px var(--glow-color)) drop-shadow(0 0 10px var(--glow-color)) drop-shadow(0 0 12px var(--glow-color)) drop-shadow(0 0 14px var(--glow-color));
}

@keyframes glow-blue {
  from {
    --glow-color: #00bfff;
  }
  to {
    --glow-color: #0080ff;
  }
}

@keyframes glow-pink {
  from {
    --glow-color: #ff00de;
  }
  to {
    --glow-color: #ff1493;
  }
}


.character-image {
  width: 90%;
  max-width: 200px;
  height: auto;
  margin: 0 auto;
  display: block;

}

.skins-section {
  margin-top: 30px;
  width: 100%;
}

.skins-section h3 {
  color: #ffd700;
  font-size: 1.5rem;
  margin-bottom: 15px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.skins-section button {
  background: linear-gradient(145deg, #3b3a34, #6c4e2e);
  color: #ffd700;
  border: 1px solid #ffd700;
  padding: 8px 12px;
  margin: 3px;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.8rem;
}

.skins-section button:hover:not(:disabled) {
  background: linear-gradient(145deg, #6c4e2e, #3b3a34);
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(255, 215, 0, 0.3);
}

.skins-section button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.invertory-button, .skill-button {
  background: linear-gradient(145deg, #ffd700, #e5c100);
  color: #3b3a34;
  border: none;
  padding: 12px 20px;
  margin-top: 20px;
  border-radius: 25px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.invertory-button:hover, .skill-button:hover {
  background: linear-gradient(145deg, #e5c100, #ffd700);
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(255, 215, 0, 0.3);
}


.bottom-section {
  padding: 20px;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: var(--background-color);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  color: var(--text-color);
}

.modal button {
  background: var(--primary-color);
  color: var(--background-color);
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
}

.modal button:hover {
  background: var(--accent-color);
}

