/* Activity cards container */
.activity-cards {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  overflow-x: auto;
  overflow-y: hidden;
  gap: 10px;
  justify-content: flex-start;
  scroll-snap-type: x mandatory;
  padding: 10px 10px;
}

/* Activity card styles */
.activity-card {
  display: flex;
  flex-direction: column;
  border: 2px solid #ffd700;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  transition: transform 0.3s, filter 0.3s;
  background: rgba(255, 255, 255, 0.2);
  max-width: 420px;
  margin: 1rem;
  position: relative;
  z-index: 2;
  flex: 0 0 auto;
  width: 240px;
  height: 360px;
  scroll-snap-align: start;
}

.activity-card:hover {
  transform: translateY(-5px);
}

.activity-card .activity-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  z-index: -1;
}

.activity-card .activity-content {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
  color: #fff;
  flex-grow: 1;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
}

.activity-card h2 {
  font-size: 1.8rem;
  margin: 0 0 1rem;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
}

.activity-card p {
  font-size: 1.2rem;
  color: #fff;
  margin-bottom: 1rem;
  flex-grow: 2;
  text-shadow: 4px 4px 8px rgba(0, 0, 0, 1.8);
}

.activity-card button {
  padding: 0.75rem 1rem;
  background-color: #32652b;
  position: absolute;
  bottom: 1rem;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: auto;
  width: 80%; /* Added to make button wider */
}

.activity-card button:hover:not(:disabled) {
  background-color: #00b36b;
}

.activity-card button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.activity-card button:focus,
.activity-card button:active {
  outline: none;
  background-color: #32652b;
  transform: translateX(-50%);
}

.activity-card .required-level {
  color: #ff6b6b;
  font-weight: bold;
}

.activity-card.level-locked:not(.inactive) .required-level {
  opacity: 1;
  visibility: visible;
}

.activity-card:not(.level-locked) .required-level,
.activity-card.inactive .required-level,
.activity-card.level-locked:not(.inactive) .required-level:not(:hover) {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

/* Show level requirement on hover for level 3+ players */
.activity-card.level-locked:not(.inactive) .required-level:hover {
  opacity: 1;
  visibility: visible;
}
[lang="fa"] {
  direction: rtl;
  text-align: right;
}

.activity-card .required-level {
  font-size: 0.9rem;
  color: #fc0000; /* Changed to a more visible color */
  text-shadow: 
        -1px -1px 0 rgb(255, 255, 255),
        1px -1px 0 black,
        -1px 1px 0 rgb(255, 255, 255),
        1px 1px 0 black;
  text-align: center;
  margin-top: auto;
  margin-bottom: 3rem; /* Added to push it above the button */
  font-weight: bold; /* Added to make it more prominent */
}

.activity-card.inactive {
  opacity: 0.7;
}

/* Hide scrollbars for a cleaner look */
.activity-cards::-webkit-scrollbar {
  display: none;
}

